import mInput from '../../components/m-input.vue'
 export default {
    components: {
        mInput
    },
    data() {
		return{
			shopObj:{},
			password:"",
			passwordok:"",
			phone:"",
			code:"",
			time:60,
			codeSend:false,
			disabled:true,
		}
	},
	methods:{
		checkPhone(phone){
			if(!(/^1[3456789]\d{9}$/.test(phone))){
				return false;
			}
			return true
		},
		sava(){
			let that=this;
			if(that.code&&that.password&&that.passwordok){
				if(that.password!=that.passwordok){
					uni.showToast({
						title:"两次输入的密码不一样",
						icon:"none"
					})
					return
				}
				that.$ajax({
					url:"/system/changePassword",
					data:{
						code:that.code,
						merchantId:that.shopObj.merchantId,
						newPassword:that.password,
						phone:that.phone,
						smsType:"SMS_161591480",
						type:1
					},
					success(d){
						uni.showToast({
							title:"修改成功",
							icon:"none"
						})
						that.code="";
						that.password="";
						that.passwordok="";
						setTimeout(function() {
							uni.navigateBack({
								delta:1
							})
						}, 1500);
						
					}
				})
			}else{
				uni.showToast({
					title:"请填写完整的信息",
					icon:"none"
				})
			}
		},
		sendCode(){
			var that=this
			if(this.codeSend){
				return;
			}
			if(!that.checkPhone(that.phone)){
				uni.showToast({
					title:"请填写正确的手机号码",
					icon:"none"
				})
			}
			this.codeSend=true;
			var t=setInterval(function(){
				that.time-=1;
				if(that.time==0){
					that.codeSend=false;
					that.time=60;
					clearInterval(t)
				}
			},1000)
			that.$ajax({
				url:"/sms",
				data:{
					phone:that.phone,
					type:"SMS_161591480",
				},success(d){
					console.log(d)
				}
			})
		}
	},
	onReady() {
		if(uni.getStorageSync("phone")){
			this.phone=uni.getStorageSync("phone");
			console.log(this.phone)
		}else{
			uni.showModal({
				title: '提示',
				content: '当前还没有安全手机,是否去添加',
				cancelText:"返回",
				confirmText:"去添加",
				success: function (res) {
					if (res.confirm) {
						uni.navigateTo({
							url:"/pages/system/phone"
						})
					} else if (res.cancel) {
						 uni.navigateBack({
							 delta:1
						 })
					}
				}
			});
		}
	},
	onLoad(e){
		let that=this;
		console.log(e.existsCashCode)
		if(e.existsCashCode=="true"){
			//修改
			uni.setNavigationBarTitle({
				title: '修改取现密码'
			});
		}else{
			uni.setNavigationBarTitle({
				title: '设置取现密码'
			});
		}
		uni.getStorage({
			key:"shopObj",
			success(res){
				that.shopObj=JSON.parse(res.data);
			}
		})
	}
}
